.button{
    color: white;
    background-color: blue;
    cursor: pointer;
    border: 1px solid blue;
    border-radius: 3px;
    padding: 3px;
}
.button:hover,
.button:focus {
    background-color: rgb(78, 66, 212);
    color: white;
    border: 1px solid blue;
    
}
.button:disabled{
    background-color: dimgrey;
    color: linen;
    opacity: 1;
}
        